iframe {
  width: 100%;
  height: 640px;
  border: 0;
	border-radius: 40px;
	margin-bottom: 10px;
}

iframe:last-child {
	margin-bottom: 0px;
}

.cbl-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 20px;
	padding-bottom: 40px;
}

.cbl-main {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.cbl-title {
	width: 85% !important;
}
