.formatives-container {
	display: flex;
	flex-direction: column;
	height: 85%;
	margin: 0;
	padding: 20px 10px;
}

.formatives-main {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.formatives-title {
	width: 85% !important;
}

.screenshots-list {
	flex-basis: 300px;
}

.formatives-image-container {
	flex-basis: 300px;
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	padding: 30px 0;
	margin: 0 -35px;
}

.formatives-image-wrapper {
	padding: 20px;
	margin: 15px;
	border: 2px solid #acacb4;
	border-radius: 40px;
	box-sizing: border-box;
	overflow: hidden;
	width: calc(100% / 3.22);
}

.formatives-image-wrapper img {
	display: block;
	width: 100%;
}

@media (max-width: 600px) {
	.screenshots-main {
		flex-basis: auto;
	}
}
